export default {
    name: "payment-code",
    components: {},
    data() {
        return {
            edit: false,
            pic_url: '',
            save_loading: false,
            price: '',
            tmp_price: '',
            is_clerk_enter: 0
        }
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.is_clerk_enter = options.is_clerk_enter ? 1 : 0;
        this.submit();
    },
    methods: {
        savePrice: function () {
            this.edit = true;
        },

        submit: function () {
            const tmp_price = this.tmp_price ? parseFloat(this.tmp_price).toFixed(2) : '';

            this.$showLoading({text: `生成中`});
            this.$request({
                url: this.$api.scan_code_pay.qr_code,
                method: 'POST',
                data: {
                    price: tmp_price,
                    is_clerk_enter: this.is_clerk_enter
                }
            }).then(info => {
                this.$hideLoading();
                if (info.code === 0) {
                    [
                        this.pic_url,
                        this.price,
                        this.tmp_price,
                        this.edit
                    ] = [
                        info.data.qr_code.file_path,
                        tmp_price,
                        tmp_price,
                        false
                    ]
                } else {
                    uni.showToast({icon: 'none', title: info.msg});
                }
            }).catch(() => {
                this.$hideLoading();
            });
        },

        //预览
        qrcodeClick: function () {
            uni.previewImage({
                urls: [this.pic_url],
            });
        },

        //保存
        saveQrcode: function () {
            const self = this;
            uni.showLoading({mask: true,title: `图片保存中`});

            self.$utils.batchSave(self.pic_url, 'image').then(result => {
                uni.showToast({title: '保存成功'});
            });
        },
    }
}
